.SendForm {
  border-top: 1px solid var(--divider-color);
  position: relative;
  padding: 10px;
  margin-top: auto;
  display: flex;
  background-color: var(--send-form-bg-color);

  &_disabled {
    opacity: 0.55;
  }

  &-Input {
    background-color: transparent;
    border: none;
    box-sizing: border-box;
    font-size: 1rem;
    color: var(--message-color-primary);
    width: 100%;
    outline: none;
  }

  &-Submit {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 10px;
    background-color: transparent;
    border: none;
    width: 18px;
    height: 18px;
    background-image: url(../../../assets/images/send-message.png);
    background-size: 100%;
    background-repeat: no-repeat;
    outline: none;

    @media (hover: hover) {
      &:hover {
        &:after {
          content: attr(data-tooltip);
          color: white;
          padding: 2px 4px;
          position: absolute;
          bottom: calc(100% + 5px);
          left: 50%;
          transform: translateX(-50%);
          background-color: var(--tooltip-bg-color);
          font-size: 0.8rem;
          border-radius: 4px;
          transition: all 0.3s ease;
          z-index: 1000;
        }
      }
    }

  }
}