.MessagesList {
  margin-top: 5px;
  margin-bottom: 5px;
  margin-right: 5px;
  padding: 15px;
  padding-right: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
  row-gap: 13px;
  overflow: auto;
  min-height: 200px;
  scrollbar-width: thin;
  scrollbar-color: var(--scrollbar-color);

  &::-webkit-scrollbar {
    width: 5px;
  }
  
  &::-webkit-scrollbar-track {
    background: transparent;
  }
  
  &::-webkit-scrollbar-thumb {
    background-color: var(--scrollbar-thumb-bg-color);
    border-radius: 20px;
  }

  &-Message {
    display: flex;
    flex-direction: column;
    row-gap: 8px;
    max-width: 70%;

    @media (max-width: 480px) {
      min-width: 85%;
    }

    @media (max-width: 360px) {
      min-width: 100%;
    }

    &_sender {
      margin-left: auto;

      .MessagesList-MessageHeader {
        margin-left: auto;
      }

      .MessagesList-MessageText {
        margin-left: auto;
        background-color: var(--message-bg-color-primary);
        color: var(--message-color-primary);
      }
    }

    &_recipient {
      margin-right: auto;

      .MessagesList-MessageText {
        margin-right: auto;
        background-color: var(--message-bg-color-secondary);
        color: var(--message-color-secondary);
      }
    }
  }

  &-MessageHeader {
    color: var(--message-header-color);
  }

  &-MessageText {
    position: relative;
    border-radius: 5px;
    padding: 5px 8px;
    word-wrap: break-word;
    white-space: pre-line;

    &_command {
      color: var(--message-command-text-color) !important;
    }
  }
}