@import '../../../variables.scss';

.Header {
  position: relative;
  display: flex;
  align-items: center;
  column-gap: 8px;
  padding: 10px 15px;
  color: var(--chat-header-color);
  border-bottom: 1px solid var(--divider-color);

  &-Name {
    line-height: 1.6em;
  }

  &-Photo {
    width: 40px;
    height: 40px;
  }

  &-Status {
    position: relative;
    width: 6px;
    height: 6px;
    background-color: $statusBgColor;
    border-radius: 50%;
    cursor: pointer;

    &:hover {
      // tooltip
      &:after {
        content: attr(data-tooltip);
        padding: 2px 4px;
        position: absolute;
        bottom: calc(100% + 5px);
        left: 50%;
        transform: translateX(-50%);
        background-color: var(--tooltip-bg-color);
        font-size: 0.8rem;
        border-radius: 4px;
        transition: all 0.3s ease;
      }
    }
  }

  &-Typing {
    position: absolute;
    bottom: 4px;
    left: 63px;
    font-size: 0.75rem;
    color: var(--chat-header-typing-color);
  }

  &-ThemeSwitcher {
    margin-left: auto;
    margin-right: 5px;
  }
}